.header-container {
  font-family: 'Gilroy';
  position: fixed;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 2%;
  z-index: 99;

  .logo-container {
    display: flex;
    align-items: center;
    cursor: pointer;

    .logo {
      height: 50px;
    }

    .title {
      color: #fff;
      font-weight: bold;
      font-size: 26px;
      line-height: 30px;
      margin-left: 16px;
    }
  }

  .links {
    color: #fff;
    font-size: 24px;
    line-height: 28px;
    display: flex;
    flex-grow: 1;
    justify-content: center;

    .link {
      opacity: 0.5;

      &+.link {
        padding-left: 45px;
      }

      &:not(.disabled):hover {
        cursor: pointer;
        opacity: 1;
      }

      &.disabled {
        user-select: none;
      }

      // &.disabled {
      //   cursor: none;
      // }
    }
  }

  .user-profile {
    height: 66px;
    border: 1px solid rgba(255, 255, 255, 0.16);
    border-radius: 16px;
    display: flex;
    padding-left: 25px;
    padding-right: 16.5px;
    align-items: center;

    &:hover {
      // cursor: pointer;
      background: #1C1E25;
    }

    .username {
      color: #fff;
      font-size: 24px;
      line-height: 28px;
      max-width: 130px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .user-avatar {
      border-radius: 50%;
      height: 38px;
      width: 38px;
      margin-left: 32px;
    }
  }

  .connect-wallet {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-overflow: ellipsis;
    color: #fff;

    // .user-profile {
    //   display: flex;
    //   align-items: center;

    //   &:hover {
    //     cursor: pointer;
    //   }

    //   .pfp {
    //     width: 32px;
    //     height: 32px;
    //     border-radius: 16px;
    //   }

    //   .user-name {
    //     margin-left: 10px;
    //     font-weight: 500;
    //     max-width: 120px;
    //     white-space: nowrap;
    //     text-overflow: ellipsis;
    //     overflow: hidden;
    //   }
    // }

    .connect-btn {
      min-width: 100px;
      padding: 8px 16px;
    }
  }
}

