.user-profile-dropdown {
  font-family: 'Gilroy';
  display: flex;
  flex-direction: column;
  background: #1C1E25;
  border: 1px solid rgba(255, 255, 255, 0.16);
  border-radius: 16px;
  padding: 40px;
  margin-top: 10px;
  text-align: center;

  .item {
    color: rgba(255, 255, 255, 0.5);
    font-size: 24px;
    line-height: 28px;
    user-select: none;

    &:not(.disabled):hover {
      color: rgba(255, 255, 255, 0.9);
      cursor: pointer;
    }

    & + .item {
      margin-top: 32px;
    }
  }

  .logout-btn {
    font-weight: 500;
    opacity: 0.85;

    &:hover {
      cursor: pointer;
      opacity: 1;
    }
  }
}